import React from 'react';
import styled from 'styled-components';

const InputDivStyled = styled.div`
  height: 2rem;
  display: flex;
  align-items: center;
  & label {
    margin-right: 0.5rem;
    line-height: 1rem;
  }
`;

const CheckboxInput = ({ label, name, value, setValue }) => (
  <InputDivStyled>
    {label && <label htmlFor={name}>{label}</label>}
    <input
      id={name}
      name={name}
      type="checkbox"
      checked={value}
      onChange={(e) => {
        setValue(e.target.checked);
      }}
    />
  </InputDivStyled>
);

export default CheckboxInput;
