import React, { useContext } from 'react';
import { useHistory } from 'react-router-dom';
import styled from 'styled-components';

import { GlobalStoreContext } from '../core/GlobalStore';

const ErrorStyled = styled.p`
  margin-top: 5rem;
  color: red;
  font-size: 1.5rem;
  font-weight: bold;
`;

const Error = () => {
  const { error, resetStore } = useContext(GlobalStoreContext);
  const browserHistory = useHistory();

  if (!error) return null;

  return (
    <ErrorStyled>
      {error}
      <button
        onClick={() => {
          browserHistory.push('/');
          resetStore();
        }}
      >
        Close
      </button>
    </ErrorStyled>
  );
};

export default Error;
