import React, { useContext } from 'react';
import { Link, useLocation } from 'react-router-dom';

import { GlobalStoreContext } from '../core/GlobalStore';
import styled from 'styled-components';
import githubLogo from '../githubLight.png';

const HeaderStyled = styled.header`
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 4rem;
  text-align: center;
  font-size: 1.5rem;
  border-bottom: solid 0.25rem #ffffff33;
  background: #24292e url(${githubLogo}) no-repeat;
  background-position: 0.5rem 0.5rem;
  background-size: 3rem;
`;

const ListStyled = styled.nav`
  width: 100%;
  height: 4rem;
  padding-left: 5rem;
  display: flex;
  align-items: center;
  list-style-type: none;
  & li:not(:first-of-type) {
    margin-left: 2rem;
  }
  & a {
    color: #f6f8fa;
    text-decoration: none;
    &:visited {
      #color: #f6f8fa;
    }
  }
`;

const Header = () => {
  const { user, error } = useContext(GlobalStoreContext);
  const { pathname } = useLocation();

  if (!user && !error) {
    return null;
  }
  return (
    <HeaderStyled>
      <nav>
        <ListStyled>
          <li>
            <Link to="/">Search</Link>
          </li>
          {pathname.startsWith('/repositories') && (
            <li>
              <Link to={`/users/${user.login}`}>{user.login}</Link>
            </li>
          )}
        </ListStyled>
      </nav>
    </HeaderStyled>
  );
};

export default Header;
