import React from 'react';
import styled from 'styled-components';

const InputDivStyled = styled.div`
  height: 2rem;
  display: flex;
  align-items: center;

  & label {
    margin-right: 0.5rem;
  }
  & input {
    width: 100%;
    max-width: 500px;
    height: 2rem;
    padding: 1rem 0.5rem;
    border: 2px solid #24292e;
    border-radius: 0.25rem;
    margin-bottom: 0.25rem;
  }
`;

const TextInput = ({ label, name, value, setValue }) => (
  <InputDivStyled>
    {label && <label htmlFor={name}>{label}</label>}
    <input
      id={name}
      name={name}
      type="text"
      value={value}
      onChange={(e) => {
        setValue(e.target.value);
      }}
    />
  </InputDivStyled>
);

export default TextInput;
