import React from 'react';
import styled from 'styled-components';

import githubLogo from '../githubLight.png';

import SearchBar from '../components/SearchBar';

const HeaderStyled = styled.header`
  width: 100%;
  height: 35vh;
  text-align: center;
  font-size: 2rem;
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  border-bottom: solid 0.5rem #ffffff33;
  background: #24292e url(${githubLogo}) no-repeat;
  background-position: 1rem 1rem;
  background-size: 5rem;
  @media (max-width: 768px) {
    background-size: 2.5rem;
  }
  @media (max-width: 300px) {
    background-size: 1rem;
  }
`;

const HeadlineStyled = styled.h1`
  padding-bottom: 1rem;
  color: white;
  @media (max-width: 768px) {
    font-size: 2rem;
  }
  @media (max-width: 300px) {
    font-size: 1rem;
  }
`;

const SectionStyled = styled.section`
  width: 100%;
  height: 65vh;
  display: flex;
  flex-flow: column;
  align-items: center;
  text-align: center;
`;

const HomePage = () => (
  <>
    <HeaderStyled>
      <HeadlineStyled>GitHub user search</HeadlineStyled>
    </HeaderStyled>
    <SectionStyled>
      <SearchBar />
    </SectionStyled>
  </>
);

export default HomePage;
