import React, { useContext, useEffect } from 'react';
import { useParams, useHistory } from 'react-router-dom';
import axios from 'axios';
import styled from 'styled-components';

import { GlobalStoreContext } from '../core/GlobalStore';
import { getUsersEndpoint } from '../api/routes';

import Loader from '../components/Loader';
import Error from '../components/Error';

const ArticleStyled = styled.article`
  padding-top: 5rem;
  width: 100%;
  display: flex;
  flex-flow: column;
  align-items: center;
`;

const ProfileDivStyled = styled.div`
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
  text-align: center;
  & img {
    display: block;
    width: 80%;
    max-width: 250px;
    border-radius: 50%;
  }
  
  & div {
    padding: 1rem;
    line-height: 2.5rem;
  }
  
  @media (max-width: 768px) {
    flex-flow: column;
  }
`

const UserPage = () => {
  const { user, error, setUser, setError } = useContext(GlobalStoreContext);
  const { username } = useParams();
  const browserHistory = useHistory();

  useEffect(() => {
    if (username && !user) {
      axios
        .get(getUsersEndpoint(username))
        .then((response) => {
          setUser(response.data);
        })
        .catch((e) => {
          e.response.status === 404
            ? setError(`User with login ${username} not found.`)
            : setError('Network error');
        });
    }
  }, [username]);

  if (error) return <Error />;

  return user ? (
    <ArticleStyled>
      <ProfileDivStyled>
        <img src={user.avatar_url} alt={`Avatar of user ${user.login}`} />
        <div>
          <h1>{user.name || 'Name missing'}</h1>
          <h2>{user.login}</h2>
          <button onClick={() => browserHistory.push(`/repositories/${user.login}`)}>
            Repositories
          </button>
        </div>
      </ProfileDivStyled>
    </ArticleStyled>
  ) : (
    <Loader />
  );
};

export default UserPage;
