import React, { useContext, useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';
import axios from 'axios';
import styled from 'styled-components';

import { GlobalStoreContext } from '../core/GlobalStore';
import { getRepositoriesEndpoint, getUsersEndpoint } from '../api/routes';

import RepositoriesList from '../components/RepositoriesList';
import TextInput from '../components/TextInput';
import CheckboxInput from '../components/Checkbox';
import Loader from '../components/Loader';
import Error from '../components/Error';

const ArticleStyled = styled.article`
  padding-top: 5rem;
  width: 50%;
  max-width: 768px;
  display: flex;
  flex-flow: column;
  align-items: center;

  @media (max-width: 768px) {
    width: 80%;
  }

  @media (max-width: 500px) {
    width: 100%;
  }
`;

const InputGroupDivStyled = styled.div`
  display: flex;
  width: 100%;
  align-items: center;
  text-align: center;
  margin: 2rem 0;

  & div:not(:last-of-type) {
    margin-right: 1rem;
  }
  @media (max-width: 500px) {
    flex-flow: column;
    align-items: start;
    padding: 0 1rem;
    margin: 0.5rem 0;

    & div:not(:last-of-type) {
      margin-bottom: 1rem;
    }
  }
`;

const Repositories = () => {
  const { user, setUser, error, setError } = useContext(GlobalStoreContext);
  const { username } = useParams();

  const [originalRepositories, setOriginalRepositories] = useState(null);
  const [filter, setFilter] = useState('');
  const [sortByStargazers, setSortByStargazers] = useState(false);
  const [filteredSortedRepositories, setFilteredSortedRepositories] = useState(null);

  useEffect(() => {
    if (!user) {
      axios
        .get(getUsersEndpoint(username))
        .then((response) => {
          setUser(response.data);
        })
        .catch((e) => {
          e.response.status === 404
            ? setError(`User with login ${username} not found.`)
            : setError('Network error');
        });
    }
    axios
      .get(getRepositoriesEndpoint(username))
      .then((response) => setOriginalRepositories(response.data))
      .catch(() => setError('Network error'));
  }, [username]);

  useEffect(() => {
    let result = originalRepositories ? [...originalRepositories] : [];
    if (filter) {
      result = result.filter((repo) => repo.name.includes(filter));
    }
    if (sortByStargazers) {
      result = result.sort((repo1, repo2) => repo2.stargazers_count - repo1.stargazers_count);
    }
    setFilteredSortedRepositories(result);
  }, [originalRepositories, filter, sortByStargazers]);

  if (error) return <Error />;

  return originalRepositories ? (
    <ArticleStyled>
      <InputGroupDivStyled>
        <TextInput name="filter" label="Filter" value={filter} setValue={setFilter} />
        <CheckboxInput
          name="sort"
          label="Sort by stargazers"
          value={sortByStargazers}
          setValue={setSortByStargazers}
        />
      </InputGroupDivStyled>
      <RepositoriesList repositories={filteredSortedRepositories || originalRepositories} />
    </ArticleStyled>
  ) : (
    <Loader />
  );
};

export default Repositories;
