import React, { useContext } from 'react';
import { useHistory } from 'react-router-dom';
import axios from 'axios';
import { Formik } from 'formik';
import * as Yup from 'yup';
import styled from 'styled-components';

import { getUsersEndpoint } from '../api/routes';
import { GlobalStoreContext } from '../core/GlobalStore';

const FormStyled = styled.form`
  width: 100%;
  margin-top: 10%;
`;

const InputStyled = styled.input`
  display: inline-block;
  width: 50%;
  max-width: 500px;
  height: 3rem;
  padding: 1rem 0.5rem;
  border: 2px solid #24292e;
  border-radius: 0.25rem;
  font-size: 1.5rem;
  margin-bottom: 0.25rem;
  
  @media (max-width: 500px) {
    width: 80%;
  }
`;

const ErrorStyled = styled.p`
  color: red;
  font-size: 1.5rem;
  font-weight: bold;
`;

const ButtonStyled = styled.button`
  margin-top: 0.5rem;
  color: #f6f8fa;
  &[disabled] {
    background-color: #94999e;
  }
`;

const SearchBar = () => {
  const { setUser } = useContext(GlobalStoreContext);
  const browserHistory = useHistory();
  return (
    <Formik
      initialValues={{ username: '' }}
      validationSchema={Yup.object().shape({
        username: Yup.string().matches(
          /^[a-z\d](?:[a-z\d]|-(?=[a-z\d])){0,38}$/i,
          'That is not valid GitHub username'
        ),
      })}
      onSubmit={async (values, { setSubmitting, setErrors }) => {
        const username = values.username;
        axios
          .get(getUsersEndpoint(username))
          .then((response) => {
            setUser(response.data);
            browserHistory.push(`/users/${username}`);
          })
          .catch((e) => {
            e.response.status === 404
              ? setErrors({ username: 'User not found' })
              : setErrors({ username: 'Network error' });

            setSubmitting(false);
          });
      }}
    >
      {({ values, errors, isSubmitting, handleChange, handleBlur, handleSubmit }) => (
        <FormStyled onSubmit={handleSubmit}>
          <InputStyled
            name="username"
            type="text"
            value={values.username}
            onChange={handleChange}
            onBlur={handleBlur}
            disabled={!!isSubmitting}
          />
          <ErrorStyled>{errors.username}</ErrorStyled>
          <ButtonStyled type="submit" disabled={!values.username.length || errors.username}>
            Search!
          </ButtonStyled>
        </FormStyled>
      )}
    </Formik>
  );
};

export default SearchBar;
